import Vue from 'vue'
// 定义全局过滤器
// 保留小数
Vue.filter('decimal', (v, n = 2) => {
  return v.toFixed(n)
})

// 格式化日期
Vue.filter('formatDate', (v, format) => {
  // console.log('v ==> ', v);
  // console.log('format ==> ', format);

  //创建日期对象
  let date = new Date(v)

  //格式化年份
  let year = date.getFullYear().toString()
  // console.log('year ==> ', year);
  if (/(y+)/.test(format)) {
    //获取匹配组的内容
    var content = RegExp.$1
    // console.log('content ==> ', content);
    format = format.replace(content, year.slice(year.length - content.length))
  }

  // console.log('format ==> ', format);

  //格式化月份、日份、时、分、秒
  let o = {
    M: date.getMonth() + 1,
    d: date.getDate(),
    h: date.getHours(),
    m: date.getMinutes(),
    s: date.getSeconds()
  }

  // console.log('o ==> ', o);

  for (let key in o) {
    //创建动态正则表达式
    let reg = new RegExp(`(${key}+)`)
    // console.log('reg ==> ', reg);

    if (reg.test(format)) {
      //获取组匹配的内容
      let groupContent = RegExp.$1
      // console.log('groupContent ==> ', groupContent);

      format = format.replace(groupContent, o[key] >= 10 ? o[key] : groupContent.length == 2 ? '0' + o[key] : o[key])
      // console.log('format ==> ', format);
    }
  }

  return format
})
