import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

// 路由懒加载
const Login = () => import('../views/Login.vue')
const Main = () => import('../views/Main.vue')
const Home = () => import('../views/Home.vue')
const Menu = () => import('../views/Menu.vue')
const Shopbag = () => import('../views/Shopbag.vue')
const My = () => import('../views/My.vue')
const Detail = () => import('../views/Detail.vue')
const Pay = () => import('../views/Pay.vue')
const NewAddress = () => import('../views/NewAddress.vue')
const Address = () => import('../views/Address.vue')
const Order = () => import('../views/Order.vue')
const Account = () => import('../views/Account.vue')
const Like = () => import('../views/Like.vue')
const Secure = () => import('../views/Secure.vue')
const Search = () => import('../views/Search.vue')

const routes = [
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/main',
    name: 'Main',
    component: Main,
    children: [
      {
        path: 'home',
        name: 'Home',
        component: Home
      },
      {
        path: 'menu',
        name: 'Menu',
        component: Menu
      },
      {
        path: 'shopbag',
        name: 'Shopbag',
        component: Shopbag
      },
      {
        path: 'my',
        name: 'My',
        component: My
      }
    ]
  },
  {
    path: '/detail/:pid',
    name: 'Detail',
    component: Detail
  },
  {
    path: '/pay',
    name: 'Pay',
    component: Pay
  },
  {
    path: '/newAddress',
    name: 'NewAddress',
    component: NewAddress
  },
  {
    path: '/address',
    name: 'Address',
    component: Address
  },
  {
    path: '/order',
    name: 'Order',
    component: Order
  },
  {
    path: '/account',
    name: 'Account',
    component: Account
  },
  {
    path: '/like',
    name: 'Like',
    component: Like
  },
  {
    path: '/secure',
    name: 'Secure',
    component: Secure
  },
  {
    path: '/search',
    name: 'Search',
    component: Search
  },
  {
    path: '*',
    redirect: {
      name: 'Home'
    }
  }
]

const router = new VueRouter({
  routes
})

export default router
